.css-treeview {
    width: 400px;
    height: 400px;
}

.css-treeview .tooltip {
    max-width: 180px;
    margin-left: 50px;
}

.css-treeview .tooltip .tooltip-inner {
    white-space: pre-wrap;
    padding: 5px;
}

.css-treeview ul,
.css-treeview li {
    padding: 0;
    margin: 0;
    list-style: none;

    line-height: 24px;
}

.css-treeview input {
    position: absolute;
    opacity: 0;
}

.css-treeview input.toggle-box {
    z-index: 10000;
}
.standard-name.stdLabel {
    max-width: 85%;
}
.css-treeview .standardSelection ~ .standard-name.stdLabel {
    left: -14px;
    position: relative;

}

.css-treeview .standardSelection + .toggle-box {
    position: relative;
    left: 3px;
    top: -7px;
}

.css-treeview .standardSelection + * {
    margin-left: 33px;
    position: relative;
}

.css-treeview .standardSelection {
    opacity: 1;
    margin-left: 16px;
    margin-top: 3px;
}

.css-treeview .stdLabel {

    /*text-overflow: ellipsis;*/
    white-space: nowrap;
    /*overflow: hidden;*/

}

.css-treeview {
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}

.css-treeview a {
    color: #00f;
    text-decoration: none;
}

.css-treeview a.select {
    color: red;
    text-decoration: none;
}

.css-treeview a:hover {
    text-decoration: underline;
}

.css-treeview input + label + ul,
.css-treeview input + label + div + ul {
    margin: 0 0 0 22px;
}

.css-treeview input + label + ul {

}

.css-treeview input ~ ul {
    display: none;
}

.css-treeview label,
.css-treeview label::before {
    cursor: pointer;
}

.css-treeview input:disabled + label {
    cursor: default;
    opacity: .6;
}

.css-treeview input.toggle-box:checked:not(:disabled) ~ ul {
    display: block;
}

.css-treeview label,
.css-treeview label::before {
    background: url("../media/icons.png") no-repeat;
}

.css-treeview label,
.css-treeview a,
.css-treeview label::before {
    display: inline-block;
    min-height: 16px;
    line-height: 16px;
    vertical-align : middle;
}

.css-treeview label {
    background-position: 18px 2px;
}

.css-treeview label::before {
    content: "";
    width: 16px;
    margin: 0 22px 0 0;
    /*vertical-align: middle;*/
    background-position: 0 -32px;
}

.css-treeview input:checked + label::before {
    background-position: 0 -16px;
}

/* webkit adjacent element selector bugfix */
@media screen and (-webkit-min-device-pixel-ratio:0) {
    .css-treeview {
        -webkit-animation: webkit-adjacent-element-selector-bugfix infinite 1s;
    }

    @-webkit-keyframes webkit-adjacent-element-selector-bugfix {
        from {
            padding: 0;
        }
        to {
            padding: 0;
        }
    }
}

.standardsTree #treeScroller {
    padding: 10px;
    border: 1px solid #ddd;
    height: 210px;
    overflow-y: auto;
    overflow-x: auto;
}

.standardsTree .selected-top-container {
    margin-top: 40px;
    padding-top: 10px;
    border-top: 1px solid #ddd;
    margin-top: 15px;
}

.standardsTree #selectedScroller {

    padding: 10px;
    border: 1px solid #ddd;
    overflow-y: auto;
    height: 135px;
    overflow-y: auto;
}

.standardsTree  #selectedContainer {
    position: relative;
}

.standardsTree  ::-webkit-scrollbar {
    width: 6px;
}

.standardsTree #treeContainer .tree-title {
    margin-bottom: 5px;

}

.standardsTree #treeContainer {
    position: relative;
}

#dialog.standardsTree {
    min-height: 540px;
    max-height: 540px;
    height: 540px;
    width: 700px;
    overflow: visible;
}

#dialog.standardsTree #dialogContent {
    min-height: 410px;
    max-height: 410px;
    padding-right: 20px;
    overflow: visible;
}

.selected-title {
    margin-bottom: 5px;
}

.standard-tree-leaf {
    left: 5px;
    position: relative;
    cursor: default;
}

#dialog.standardsTree .package-container {
    margin-bottom: 15px;
}

.standard-name {
    white-space: nowrap;
    overflow: visible;
    /*text-overflow: ellipsis;*/

}
span[rel=tooltip] {
    /*text-overflow: ellipsis;*/
    white-space: nowrap;
    /*overflow: hidden;*/
    max-width:89%;
    display: inline-block;
    line-height: 20px;
}
label.standard-name span {
    top: 0;
    left: -5px;
    position: relative;
}