#standards_list ul {
    list-style:none;
}


#standards_list input[type=checkbox] {
    margin-right: 0.5em;
    bottom: 3px;
    position: relative;
}


.list_title {
    text-decoration:underline;
}

#standards_list  .list_container  li:hover {
    background-color: #DFE8E5;
}
 #standards_list  .list_container  li:hover .removeStd {
     visibility: visible;
 }

#standards_list  .list_container li {
    position:relative;
    padding:1px;
}
#standards_list  .list_container  li .removeStd:hover  {
    color:#0088cc;

}

#standards_list .list_container ul {
    margin: 5px 0;
    width: 80%;
}

.removeStd {
    position:absolute;
    right:5px;
    cursor: pointer;
    visibility: hidden;
    line-height: 22px;
}

.removeStd.disabled {
    display:none;
}