#dialog.importLesson {
	width: 60%;
	height: 70%;
	max-height: 70%;
	min-height: 70%;
	left: 20%;
	top: 15%;
	margin: 0;
}

#dialog.importLesson #dialogContent {
	max-height: calc(100% - 140px);
	min-height: calc(100% - 140px);
}

#import_lesson_container {
	font-size: 17px;
	font-weight: bold;
}

#import_lesson_container ul {
	list-style: none;
}

#import_lesson_container .warning {
	color: red;
	font-size: 1.2em;
	font-weight: bold;
	text-align: center;
}
#import_lesson_container div.course-list {
	cursor: pointer;
}

#import_lesson_container div.course-list.treeNode {
	padding: 3px;
}

#import_lesson_container div.course-list.treeNode.opened {
	background-color: #d8e8fc;
}

#import_lesson_container div.course-list.treeNode:before,
#import_lesson_container li.toc-item.treeNode:before{
	content: '\0000a0';
	width: 20px;
	height: 15px;
	background: url(../media/CGS_icons.png?ver=35) -46px -37px no-repeat;
	display: inline-block;
}

#import_lesson_container div.course-list.treeNode.opened:before,
#import_lesson_container li.toc-item.treeNode.opened:before {
	background: url(../media/CGS_icons.png?ver=35) -46px -7px no-repeat;
}

#import_lesson_container {
    width: 95%;
    font-family:"open_sans";
}

#import_lesson_container ul.lessons-list {
	display: none;
}

#import_lesson_container a.lesson-link {
	cursor: pointer;
	color: #333;
	font-size: 17px;
	font-weight: normal;
}

#import_lesson_container a.lesson-link:before {
	content: '\0000a0';
	background: url(../media/CGS_icons.png?ver=35) no-repeat;
	width: 26px;
	height: 20px;
	display: inline-block;
}

#import_lesson_container a[lessontype="bookalive"].lesson-link:before {
	background-position: -46px -101px;
}

#import_lesson_container a[lessontype="assessment"].lesson-link:before {
	background-position: -3px -25px;
	background-size: 150px 229px;
}

#import_lesson_container a[lessontype="borndigital"].lesson-link:before {
	background-position: -75px -101px;
}

#import_lesson_container a.lesson-link:hover,
#import_lesson_container a.lesson-link.selected {
	color: #276CC7;
	text-decoration: none;
}

.importLesson #dialogContent {
	padding-top: 20px;
	overflow:auto;
}

.toc-list {
    display:none;
    margin-top: 3px;
}

li.toc-item {
    cursor:pointer;
}

.courses-list li {
    margin-top: 5px;
}

ul.courses-list{
	margin-left: 10px;
}

.importLesson  ::-webkit-scrollbar {
    width: 6px;
}

#dialog.importLessonLevels {
	max-height: 400px;
	min-height: 400px;
	margin-right: -200px;
}

#dialog.importLessonLevels #dialogContent {
	min-height: 280px;
	max-height: 280px;
}

#dialog.importLessonLevels table select {
	margin-bottom: 2px;
}

#dialog.importLessonLevels .import-note {
	font-size: 11px;
}

#dialog.importLesson .inProgress {
	display: none;
}

#dialog.importLesson ul.lessons-list li.disabled {
	color: #b0b0b0;
	cursor: default;
}


#dialog.importLesson .search-filter-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-left: 10px;
}

#dialog.importLesson .search-container {
    position: relative; 
}

#dialog.importLesson .search-container .search-bar {
	background-color: #fff;
	padding: 3px 22px 3px 10px;
	border: 1px solid #d9e9fd;
	width: 290px;
	font-size: 14px;
	color: #000;
}

#dialog.importLesson .search-container .search-bar:focus {
	border-color: #4391f7;
}

#dialog.importLesson .search-container .magnifying-glass {
	background-image: url("../media/landingPopup/search icon.png");
	width: 17px;
    height: 14px;
    position: absolute;
    right: 5px;
    top: 6px;
}