#dialog.newCourse {
  max-width: 88%;
  max-height: 88%;
  min-width: 480px;
  min-height: 380px;
  width: auto;
  height: auto;
  padding: 0px;
  border: solid 1px #E4DFDF; }
  #dialog.newCourse #dialogTitle {
    padding: 0px;
    box-shadow: none;
    background-color: #f4f8fa;
    border-bottom: solid 1px #E4DFDF;
    height: 50px; }
    #dialog.newCourse #dialogTitle h3 {
      color: #2D3033;
      font-weight: bold;
      font-size: 18px;
      padding-left: 20px;
      line-height: 40px;
      padding-top: 7px; }
  #dialog.newCourse #dialogContent {
    padding: 0;
    max-width: none;
    max-height: none;
    min-width: 0;
    min-height: 0;
    width: 100%; }
    #dialog.newCourse #dialogContent form {
      padding-top: 0px; }
      #dialog.newCourse #dialogContent form button {
        display: block;
        margin: 0 auto;
        border: 1px solid #4391f7;
        border-radius: 3px;
        width: 240px;
        height: 34px;
        background-color: #4391f7;
        color: #fff;
        font-size: 16px; }
        #dialog.newCourse #dialogContent form button:disabled {
          opacity: 0.7; }
      #dialog.newCourse #dialogContent form .form-control {
        font-size: 14px;
        color: #2D3033; }
        #dialog.newCourse #dialogContent form .form-control:disabled {
          color: #c2c2c2; }
        #dialog.newCourse #dialogContent form .form-control.input-xlarge {
          width: 95%; }
        #dialog.newCourse #dialogContent form .form-control#newCourseName {
          direction: ltr !important; }
          #dialog.newCourse #dialogContent form .form-control#newCourseName.rtl {
            direction: rtl !important; }
      #dialog.newCourse #dialogContent form select.form-control {
        color: #999999; }
        #dialog.newCourse #dialogContent form select.form-control.has-value {
          color: #2D3033; }
      #dialog.newCourse #dialogContent form #last-row {
        margin-top: 60px;
        margin-bottom: 0px;
        position: relative; }
        #dialog.newCourse #dialogContent form #last-row label {
          color: #939091;
          font-size: 12px;
          font-weight: 500; }
        #dialog.newCourse #dialogContent form #last-row:after {
          border-top: 1px solid #E4DFDF;
          content: "";
          margin: 0 auto;
          position: absolute;
          top: -18%;
          left: -40px;
          right: 0;
          bottom: 0;
          width: 478px; }
      #dialog.newCourse #dialogContent form #button_row {
        padding-top: 17px; }
  #dialog.newCourse #dialogControls {
    display: none; }
  #dialog.newCourse .form-group {
    margin: 30px 30px 30px 40px; }
    #dialog.newCourse .form-group label {
      font-size: 12px;
      font-weight: bold;
      color: #2D3033;
      margin: 0px;
      position: relative; }
    #dialog.newCourse .form-group.required label:before {
      content: "*";
      color: #4391f7;
      font-size: 14px;
      font-weight: 600;
      position: absolute;
      left: -11px;
      top: 0px; }
